import React from 'react';
import './stylesheet.css';
import { db, auth, storageRef } from './firebase';
import { Link } from 'react-router-dom';
import SearchBox from './searchBox';

class Restaurants extends React.Component {
  state = {
    restaurants: null,
    filteredList: []
  };

  componentDidMount() {
    console.log('mounted');
    db.collection('restaurants')
      .get()
      .then((snapshot) => {
        const restaurants = [];
        snapshot.forEach((doc) => {
          const data = doc.data();
          data['id'] = doc.id;
          data['categories'] = doc.id.categories;
          restaurants.push(data);
        });
        this.setState({ restaurants: restaurants, filteredList: restaurants });
        console.log(restaurants);
      })
      .catch((error) => console.log(error));
  }

  handleSearch = (e) => {
    const { restaurants } = this.state
    const input = (e.target.value).toLowerCase()
    var regex = new RegExp("^.*" + input + ".*$");
    var filtered = restaurants.filter((item) => {
      let name = item.name.toLowerCase()
      return name.match(regex)
    })
    this.setState({ filteredList: filtered })
  }

  render() {
    return (
      <div>
        <SearchBox handleSearch={this.handleSearch} />
        <div className='App'>
          {this.state.filteredList !== null ? (
            this.state.filteredList &&
            this.state.filteredList.map((restaurant) => {
              return (
                <a className="box_animation animate__fadeIn">
                  <Link
                    to={{
                      pathname: `/restaurants/${restaurant.id}`,
                      state: {
                        name: `${restaurant.name}`,
                        city: `${restaurant.city}`,
                        state: `${restaurant.state}`,
                        cuisine: `${restaurant.cuisine}`,
                        street: `${restaurant.street}`,
                        zip: `${restaurant.zip}`,
                        phone: `${restaurant.phone}`,
                        id: `${restaurant.id}`,
                        logo_url: `${restaurant.logo_url}`,
                        storeFrontImage_url: `${restaurant.storeFrontImage_url}`,
                      },
                    }}
                  >
                    <div class='restaurant-box'>
                      <img
                        id='restaurant-logo'
                        src={restaurant.logo_url}
                        alt=''
                      />
                      <div>
                        <h3>{restaurant.name}</h3>
                        <span>{restaurant.cuisine}</span>
                        <p>
                          {restaurant.city}, {restaurant.state}
                        </p>
                      </div>
                    </div>
                  </Link>
                </a>
              );
            })
          ) : (
              <div class='spinner-border text-danger' role='status'>
                <span class='sr-only'>Loading...</span>
              </div>
            )}
        </div>
      </div>
    );
  }

  async loadRestaurantLogo(restaurant_id, restaurant_name) {
    storageRef
      .child(
        '/restaurants/' +
        restaurant_id +
        '/logo/' +
        restaurant_name +
        ' Logo.png'
      )
      .getDownloadURL()
      .then(function (url) {
        var img = document.getElementById(restaurant_name + '.png');
        var returnVal = url;
        //console.log(restaurant_name + " -> " + returnVal);
        return returnVal;
      })
      .catch(function (error) {
        //console.log(error)
        //console.log("Broken ---->" + restaurant_id + " -------> " + restaurant_name)
      });
  }
}

// <Link to={{ pathname: '/route', state: { foo: 'bar'} }}>

// <Link to={`/seefood-app/restaurants/${restaurant.name}`} params={{ restaurants: `${restaurant.name}` }}></Link>

export default Restaurants;
