import React from 'react';
import './stylesheet.css';
import MenuBox from './menuBox';
import Nav from './components/navBar';
import Footer from './components/footer';
import { db } from './firebase';

class Menu extends React.Component {
  state = {
    menu: {},
  };

  componentDidMount() {
    const id = this.props.match.params.id;
    db.collection(`restaurants`)
      .get()
      .then((snapshot) => {
        const restaurants = [];
        snapshot.forEach((doc) => {
          if (doc.id === id) {
            const data = {
              id: doc.id,
              ...doc.data(),
            };
            this.setState({ menu: data });
          }
        });
      })
      .catch((error) => console.log(error));
  }
  render() {
    const id = this.props.match.params.id;
    console.log(this.state.menu);
    return (
      <div>
        <Nav />
        <div id='restaurant-storefront'>
          <section
            class='menu-hero'
            style={{
              backgroundImage: `url(${this.state.menu ? this.state.menu.storeFrontImage_url : ''
                })`,
            }}
          >
            <div class='hero-container'>
              <img
                id='restaurant-logo'
                src={this.state.menu ? this.state.menu.logo_url : ''}
                alt=''
              />
            </div>
          </section>
        </div>

        <div class='menu-restaurant-info'>
          <div>
            <h2>{this.state.menu ? this.state.menu.name : ''}</h2>
            <p>
              {this.state.menu ? this.state.menu.street : ''}
            </p>
            <p>
              {this.state.menu ? this.state.menu.city : ''}{', '}
              {this.state.menu ? this.state.menu.state : ''}{' '}{this.state.menu ? this.state.menu.zip : ''}
            </p>
            <div class='button-container'>
              <a
                href={`tel: ${this.state.menu ? this.state.menu.phone : ' '}`}
                rel='noopener noreferrer' >
                Call Now
              </a>
            </div>
          </div>
        </div>

        <div class='menu-category-grid'>
          <MenuBox id={id} />
        </div>
        <Footer />
      </div >
    );
  }
}

export default Menu;
