import React from 'react';
import './stylesheet.css';
import { db, auth } from './firebase';
import { Link } from 'react-router-dom';

class MenuBox extends React.Component {
  state = {
    category: null,
  };

  componentDidMount() {
    console.log('mounted');
    db.collection('restaurants')
      .doc(`${this.props.id}`)
      .collection('categories')
      .get()
      .then((snapshot) => {
        const category = [];
        snapshot.forEach((doc) => {
          const data = doc.data();
          data['id'] = doc.id;
          category.push(data);
        });
        this.setState({ category: category });
        console.log(category);
      })
      .catch((error) => console.log(error));
  }

  render() {
    const saveToLocalStorage = (cat) => {
      localStorage.setItem('category', JSON.stringify(cat));
    };
    return (
      <div>
        {this.state.category !== null ? (
          this.state.category &&
          this.state.category.flatMap((category) => {
            return (
              <a>
                <Link
                  onClick={() => saveToLocalStorage(category)}
                  to={{
                    pathname: `/restaurants/${this.props.id}/${category.id}/${category.name}`,
                    state: {
                      name: `${category.name}`,
                      image: `${category.image_url}`,
                      id: {
                        id: `${this.props.id}`,
                        categoryid: `${category.id}`,
                      },
                    },
                  }}
                >
                  <div class='menu-category'>
                    <div class='menu-category-info'>
                      <img id='restaurant-category' src={category.image_url} />
                      <div class='title'>
                        <h4>{`${category.name}`}</h4>
                      </div>
                    </div>
                  </div>
                </Link>
              </a>
            );
          })
        ) : (
          <div class='spinner-border text-danger' role='status'>
            <span class='sr-only'>Loading...</span>
          </div>
        )}
      </div>
    );
  }
}

export default MenuBox;
