import React, { Fragment } from 'react';
import './stylesheet.css';
import { db, auth } from './firebase';
import { useState } from 'react';
import { useEffect } from 'react';
import NumberFormat from 'react-number-format';
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';

const MenuItem = (props) => {
  const [state, setState] = useState({
    category: null,
    modalData: null,
    thumbnail: '',
  });

  const getCategory = async () => {
    console.log(props);
    console.log('mounted');
    const lengthOfCategory = await db
      .collection('restaurants')
      .doc(props.id.id)
      .collection('categories')
      .get();
    const category = [];
    const data = await db
      .collection('restaurants')
      .doc(props.id.id)
      .collection('categories')
      .doc(props.id.categoryid)
      .get();

    console.log(data.data());

    setState({ category: data.data() });
  };

  const openModal = (data) => {
    setState({ ...state, modalData: data });
  };

  const closeModal = () => {
    setState({ ...state, modalData: null, thumbnail: '' });
  };

  const openImageView = (image) => {
    setState({ ...state, thumbnail: image });
  };
  useEffect(() => {
    getCategory();
  }, [props]);

  useEffect(() => {
    const body = document.querySelector('body');

    if (state.modalData) {
      disableBodyScroll(body);
    } else if (!state.modalData) {
      enableBodyScroll(body);
    }
    return () => enableBodyScroll(body);
  }, [state.modalData]);

  return (
    <Fragment>
      <div
        style={{
          transform: state.modalData
            ? `translateY(${0 + '%'})`
            : `translateY(${100 + '%'})`,
        }}
        className='menu-overlay'>

        {!state.thumbnail ? (
          <>
            <div className='overlay-information'>
              <span className='close-overlay-modal' onClick={closeModal}>
                <i class="far fa-times-circle fa-2x"></i>
              </span>
              <h3 className='title'>
                {state.modalData ? state.modalData.name : null}
              </h3>
              {state.modalData && state.modalData.image_url ? (
                <div className='overlay-iamge-container'>
                  <img
                    onClick={() => openImageView(state.modalData.image_url)}
                    src={state.modalData.image_url}
                    alt=''
                  />
                </div>
              ) : null}
              <NumberFormat class='numberformat' value={state.modalData
                ? + state.modalData.price
                : null} prefix={'$'} displayType={'text'} decimalScale='2' fixedDecimalScale='true' decimalSeparator='.' />
              <pre>{state.modalData ? state.modalData.description : null}</pre>
            </div>
          </>
        ) : null}
        {state.thumbnail ? (
          <div className='overlay-image-viewer'>
            <img className='image-viewer-img' src={state.thumbnail} />
          </div>
        ) : null}
      </div>
      <div class='menu-items-grid'>
        {state.category !== null ? (
          state.category.items ? (
            state.category.items.map((item, index) => {
              console.log(item);
              return (
                <a key={index} onClick={() => openModal(item)}>
                  <div class='menu-item' >
                    <a class='img-pop-up' >
                      <img src={item.image_url} />
                    </a>
                    <div class='menu-item-info'>
                      <div class='menu-item-top'>
                        <h3>{item.name}</h3>
                        <NumberFormat class='numberformat' value={item.price || ''} prefix={'$'} displayType={'text'} decimalScale='2' fixedDecimalScale='true' decimalSeparator='.' />
                      </div>
                      <pre>{item.description || ''}</pre>
                    </div>
                  </div>
                </a>
              );
            })
          ) : state.category.categories ? (
            state.category.categories.map((cat, i) => {
              return (
                <>
                  <div id='restaurant-subcategory-bg' >
                    <div
                      class='menu-items-header' style={{ backgroundImage: `linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(${cat.image_url})` }} >
                      <h3>{cat.name}</h3>
                    </div>
                  </div>
                  {cat.items
                    ? cat.items.map((val, l) => (
                      <>
                        <a key={i} onClick={() => openModal(val)}>
                          <div class='menu-item'>
                            <a class='img-pop-up'>
                              <img src={val.image_url} />
                            </a>
                            <div class='menu-item-info'>
                              <div class='menu-item-top'>
                                <h3>{val.name}</h3>
                                <NumberFormat class='numberformat' value={val.price || ''} prefix={'$'} displayType={'text'} decimalScale='2' fixedDecimalScale='true' decimalSeparator='.' />
                              </div>
                              <pre>{val.description || ''}</pre>
                            </div>
                          </div>
                        </a>
                      </>
                    ))
                    : null}
                </>
              );
            })
          ) : null
        ) : (
            <div class='spinner-border text-danger' role='status'>
              <span class='sr-only'>Loading...</span>
            </div>
          )}
      </div>
    </Fragment>
  );
};

export default MenuItem;
