import React from 'react';
import './stylesheet.css';
import StoryImageOne from './images/section_images/sf_story_image_1.png';
import StoryImageTwo from './images/section_images/sf_story_image_2.png';
import gurpreet from './images/team_images/gurpreet.png'
import luis from './images/team_images/luis.png'
import kyle from './images/team_images/kyle.png'
import frank from './images/team_images/frank.png'
import joseph from './images/team_images/joseph.png'
import robert from './images/team_images/robert.png'
import carlos from './images/team_images/carlos.png'
import elbert from './images/team_images/elbert.png'
import samuel from './images/team_images/samuel.png'
import jesus from './images/team_images/jesus.png'
import satvik from './images/team_images/satvik.png'
import vaibhav from './images/team_images/vaibhav.png'
import Nav from './components/navBar'
import Footer from './components/footer';

function About() {
    return (
        <div>
            <Nav />
            <section class="hero about-hero">
                <div class="hero-container">
                    <h1>Meet the Team</h1>
                    <p>The SeeFood Team is filled with talented individuals. See for yourself.</p>
                </div>
            </section>

            <section class="story">
                <div class="story-container">
                    <div class="image-container col-lg-6 ">
                        <img src={StoryImageOne} alt="Restaurant photo" />
                        <img src={StoryImageTwo} alt="Restaurant photo" />
                    </div>
                    <div class="text-container col-lg-6 col-sm-12">
                        <h2>Our Story</h2>
                        <p>The idea for SEEFOOD came a few years ago when I was out-of-town visiting a friend in Chico, CA. It
                        was my first time in the area and I had no idea what to eat. We ended up at a small authentic
                        Japanese restaurant where the menus were in a foreign language. Our group of friends was confused
                        and had no idea how we were going to figure out what to order. The waitress saw our confusion and
                        brought over a binder with photos of their dishes. This helped a bit but now had to wait for the
                        binder to make its way around the table. I was hungry and getting anxious.

                        It was in that moment that I realized how useful it would be to have photos and info for restaurant
                        menus through my mobile phone.

                        Soon after my trip i tried to find a solution for this problem, but I could not find one. I tried to
                        use sources like Yelp and TripAdvisor. Yelp was great for finding a new restaurant but that’s where
                        that ended. Yelp doesn’t have full menus, sometimes has pictures but they are not always good
                        quality and aren’t labeled with dish names or organized. TripAdvisor was similar but not consistent
                        either. And so SEEFOOD was born. </p>
                    </div>
                </div>
            </section>

            <div class="team-grid">
                <div class="team-box">
                    <img src={robert} alt="Photo of Roberto Guerrero Jr" />
                    <div class="team-box-info">
                        <h3>Roberto Guerrero Jr</h3>
                        <p class="italicize">Founder and Business Development</p>
                        <p>I believe that experiences are what make life enjoyable. I have been fortunate enough to learn what my passion is and what motivates me. My passion is technology and I am motivated to build product that helps create experiences for people.  Some of my fondest memories have centered around the experience of dinning out and enjoying a meal with friends and family. I started SeeFood because I believe we can help make this experience even better.</p>
                        <div>
                            <a href="https://www.linkedin.com/in/roberto-guerrero-jr-92235047/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-linkedin fa-2x"></i>
                            </a>
                            <a href="https://www.instagram.com/r0bgj/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-instagram fa-2x"></i>
                            </a>
                        </div>
                    </div>
                </div>
                <div class="team-box">
                    <img src={joseph} alt="Photo of Joseph Molina" />
                    <div class="team-box-info">
                        <h3>Joseph Molina</h3>
                        <p class="italicize">Co-Founder and Lead Android Engineer</p>
                        <p>“It’s up to you to make each day a good day!” That is a quote I live by every day.I learned early on that there is no such thing as a “bad day”. You can always have a good day if you have the right mindset! I am a first-generation college graduate and Software Engineer. I love building things that help the world. I joined SeeFood because I believe it will change the way people dine in and improve the dining experience. I’m excited to implement features that will help people of different languages!</p>
                        <div>
                            <a href="https://www.linkedin.com/in/josephmolina1/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-linkedin fa-2x"></i>
                            </a>
                            <a href="https://www.instagram.com/joemoemoney88/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-instagram fa-2x"></i>
                            </a>
                        </div>
                    </div>
                </div>
                <div class="team-box">
                    <img src={jesus} alt="Photo of Jesus Bernal" />
                    <div class="team-box-info">
                        <h3>Jesus Bernal</h3>
                        <p class="italicize">Co-Founder and iOS Engineer</p>
                        <p>Hi, my name is Jesus and I am a recent graduate from CSU Monterey Bay. I care about making the app accessible to everyone and look forward to improving on it every step of the way. A few things I enjoy doing are; browsing Reddit, watching basketball (Lakers) and hitting buttons on my keyboard.</p>
                        <div>
                            <a href="https://www.linkedin.com/in/jesus-andres-bernal-lopez/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-linkedin fa-2x"></i>
                            </a>
                        </div>
                    </div>
                </div>
                <div class="team-box">
                    <img src={kyle} alt="Photo of Kyle Contreras" />
                    <div class="team-box-info">
                        <h3>Kyle Contreras</h3>
                        <p class="italicize">Lead Photographer</p>
                        <p>Kyle has a degree in business administration in concentration in accounting from Cal state Monterey bay. However, his passion has been photography.  He switched his interests from adventure photography to food photography ever since joining Seefood. Other things he is passionate about is his family, friends, and the art of becoming a better than he was the day prior.</p>
                        <div>
                            <a href="https://www.linkedin.com/in/ian-kyle-contreras-2b111a16b/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-linkedin fa-2x"></i>
                            </a>
                            <a href="https://www.instagram.com/ik_contreras/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-instagram fa-2x"></i>
                            </a>
                        </div>
                    </div>
                </div>
                <div class="team-box">
                    <img src={carlos} alt="Photo of Carlos Alvarez" />
                    <div class="team-box-info">
                        <h3>Carlos Alvarez</h3>
                        <p class="italicize">Senior Photographer</p>
                        <p>Carlos is from the Monterey Bay area. Photography started as a hobby and over time turned into something more. Macro shots are his favorite style of shooting pictures. He also loves sports and is a car enthusiast.</p>
                        <div>
                            <a href="https://www.instagram.com/kingcharlie36/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-instagram fa-2x"></i>
                            </a>
                        </div>
                    </div>
                </div>
                <div class="team-box">
                    <img src={elbert} alt="Photo of Elbert Ramos" />
                    <div class="team-box-info">
                        <h3>Elbert Ramos</h3>
                        <p class="italicize">Cinematographer</p>
                        <p>My name is Elbert I was born and raised in LA but have been living in Salinas for 4 years now. I developed a love for video at a very young age which eventually turned into my passion. Happy and blessed to be part of the seefood team. I get to create meaningful and exciting content, looking forward to the future of SeeFood!</p>
                        <div>
                            <a href="https://www.instagram.com/elbertwiththee/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-instagram fa-2x"></i>
                            </a>
                        </div>
                    </div>
                </div>
                <div class="team-box">
                    <img src={luis} alt="Photo of Luis Saldana" />
                    <div class="team-box-info">
                        <h3>Luis Saldana</h3>
                        <p class="italicize">Intern; Android Development</p>
                        <p>Hello my name is Luis Saldaña Torres, I am currently interning at SeeFood as an Android Developer and a rising senior at CSU Stanislaus majoring in Business Administration with an emphasis in Computer Information Systems. Through  application and website development, and natural curiosity I have made it my goal to create stepping stones in technology for generations to come. Ultimately I strive to explore the intersection of technology and our daily lives by using the skills I have developed inside the classroom, through multiple projects, and through self taught courses to facilitate daily life activities. I thrive in an environment where creativity is essential for customer satisfaction while working part time as a home remodeler, and in my free time I enjoy exploring Mother Nature and the breathtaking landscapes it has to offer.</p>
                    </div>
                </div>
                <div class="team-box">
                    <img src={frank} alt="Photo of Frank Duenez" />
                    <div class="team-box-info">
                        <h3>Frank Duenez</h3>
                        <p class="italicize">Android Development</p>
                        <p>I am interning at SeeFood as an Android developer, and I am a recent undergraduate from California State University, Monterey Bay. In my spare time, I like to go out hiking with my friends and going to restaurants I have never been to before.</p>
                        <div>
                            <a href="https://www.linkedin.com/in/frank-duenez/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-linkedin fa-2x"></i>
                            </a>
                            <a href="https://www.instagram.com/frankthetank1749/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-instagram fa-2x"></i>
                            </a>
                        </div>
                    </div>
                </div>
                <div class="team-box">
                    <img src={gurpreet} alt="Photo of Gurpreet Sidhu" />
                    <div class="team-box-info">
                        <h3>Gurpreet Sidhu</h3>
                        <p class="italicize">Backend Developer</p>
                        <p>Hey there, I'm Gurpreet. A little about me: I am a newly minted Software Engineer and Computer Scientist, excited to make my way into the world. I've also worked for the better part of the last decade as a Pharmacy Tech. I am an avid fan of the Science Fiction and Fantasy genre, print or screen.. it doesn't matter! I'm an aspiring writer myself and a sometimes graphic artist. You could say I wear many hats, but usually it's just a beanie.</p>
                        <div>
                            <a href="https://www.linkedin.com/in/sid-who/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-linkedin fa-2x"></i>
                            </a>
                        </div>
                    </div>
                </div>
                <div class="team-box">
                    <img src={samuel} alt="Photo of Sam Ramirez" />
                    <div class="team-box-info">
                        <h3>Sam Ramirez</h3>
                        <p class="italicize">Lead Web Design and Development </p>
                        <p>I’m a simple guy who loves to swipe through Pinterest all day. I’ve always had a keen interest in design, but I never knew what UX stood for. But lucky, I figured it out. Now I continue to work on web development projects as well as graphic design!</p>
                        <div>
                            <a href="https://www.linkedin.com/in/07070707/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-linkedin fa-2x"></i>
                            </a>
                            <a href="https://www.instagram.com/thesamyool/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-instagram fa-2x"></i>
                            </a>
                        </div>
                    </div>
                </div>
                <div class="team-box">
                    <img src={satvik} alt="Photo of Satvik Khopkar" />
                    <div class="team-box-info">
                        <h3>Satvik Khopkar</h3>
                        <p class="italicize">Intern; Web Development</p>
                        <p>My name is Satvik Khopkar and I am a 4th year Computer Science student at California State University, Monterey Bay. I am from India and I came to the United States in 2018. I'm passionate about front end development, because I’mdriven to make life better, and I’m fascinated by how technology can be used to achieve that. During my spare time, I love to watch soccer and movies. On weekends, you will most probably find me playing soccer with my friends.</p>
                        <div>
                            <a href="https://www.linkedin.com/in/sid-who/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-linkedin fa-2x"></i>
                            </a>
                            <a href="https://www.instagram.com/khopkarrr/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-instagram fa-2x"></i>
                            </a>
                        </div>
                    </div>
                </div>
                <div class="team-box">
                    <img src={vaibhav} alt="Photo of Vaibhav Nagre" />
                    <div class="team-box-info">
                        <h3>Vaibhav Nagre</h3>
                        <p class="italicize">Intern; Web Development</p>
                        <p>Hey guys!! <br /> I am interning at Seefood as a Web Developer. Also, I am a Senior at California State University Monterey Bay majoring in Computer Science with a concentration of Software Engineering. Apart from that, I like to play soccer in my free time. Also I am a wildlife photographer.</p>
                        <div>
                            <a href="https://www.linkedin.com/in/sid-who/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-linkedin fa-2x"></i>
                            </a>
                            <a href="https://www.instagram.com/vaibhav.1427/" target="_blank" rel="noopener noreferrer">
                                <i class="fab fa-instagram fa-2x"></i>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
            <Footer />
        </div>
    );
}

export default About;