import React from 'react'
import '../stylesheet.css'
import logo from '../images/sf_images/seefood_logo_white.png'
import appStore from '../images/app-store-button/Download-on-the-App-Store-button.png'

function Nav() {
    return (
        <div class="navbar-container">
            <nav class="navbar navbar-expand-lg navbar-dark">
                <a href="/home">
                    <img src={logo} alt="" />
                </a>
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <div class="collapse navbar-collapse" id="navbarText">
                    <ul class="navbar-nav mr-auto">
                        <li class="nav-item active">
                            <a class="nav-link" href="/home">Home<span class="sr-only">(current)</span></a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" href="/restaurants">Restaurants</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" href="/about">About Us</a>
                        </li>
                    </ul>
                    <span class="navbar-text">
                        <ul>
                            <li class="nav-item dropdown"><a href="https://twitter.com/SeeFood_app" target="_blank" rel="noopener noreferrer"><i class="fab fa-twitter"></i></a></li>
                            <li class="nav-item dropdown"><a href="https://www.facebook.com/SeeFoodApplication/" target="_blank" rel="noopener noreferrer"><i
                                class="fab fa-facebook"></i></a></li>
                            <li class="nav-item dropdown"><a href="https://www.instagram.com/seefood_app/?hl=en" target="_blank" rel="noopener noreferrer"><i
                                class="fab fa-instagram"></i></a></li>
                            <li id="appstore-icon" class="nav-item dropdown"><a href="itms-apps://itunes.apple.com/us/app/id1525052306" rel="noopener noreferrer"><img src={appStore} /></a></li>
                        </ul>
                    </span>
                </div>
            </nav>
        </div>
    );
}

export default Nav;

